type Template = {
  css?: string
  className?: string
}

export const Url = ({ className, css }: Template) => {
  const urlCss = `
    text-decoration: underline;
    cursor: pointer;
  `

  const regex = RegExp(
    '[-a-zA-Z0-9@:%._+~#=]{1,256}.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_+.~#?&//=]*)'
  )

  return {
    text: regex,
    css: css || urlCss,
    className: className || '',
    tag: 'a'
  }
}
